import React, { HTMLAttributes } from 'react';
import cx from 'classnames';
import classNames from 'classnames';

import { Container } from '../Container';
import { Background } from '../types';

import styles from './ImageSection.module.scss';

export interface ImageSectionProps
    extends Omit<HTMLAttributes<HTMLElement>, 'className'> {
    children: React.ReactNode;
    direction?: 'row' | 'row-reverse';
    layout?: 'half' | 'imageTwoThirds' | 'contentTwoThirds';
    background?: Background;
}

export const ImageSection: React.FC<ImageSectionProps> = ({
    children,
    direction = 'row',
    layout = 'half',
    background,
    ...rest
}) => {
    // Remove unnecessary props
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { className, ...sectionProps } = rest as HTMLAttributes<HTMLElement>;

    return (
        <section
            className={classNames(styles.base, className, {
                [styles['base--alternative']]: background === 'alternative',
            })}
            {...sectionProps}
        >
            <Container>
                <div
                    className={cx(
                        styles['two-column-section'],
                        styles[`two-column-section--${direction}`],
                        styles[`two-column-section--${layout}`]
                    )}
                >
                    {children}
                </div>
            </Container>
        </section>
    );
};

export const ImageSectionImage: React.FC<{ children: React.ReactNode }> = ({
    children,
}) => <div className={cx(styles['image-column'])}>{children}</div>;

export const ImageSectionContent: React.FC<{ children: React.ReactNode }> = ({
    children,
}) => <div className={cx(styles['content-column'])}>{children}</div>;
